.drop-container {
  position: relative;
  display: flex;
  justify-content: center;
  align-items: center;
  flex-wrap: wrap;
  padding: 50px 0;
  grid-gap: 40px 80px;
  gap: 40px 80px;
  margin-top: 20px;
}

.drop-image1 {
  display: flex;
  justify-items: center;
  align-items: center;
  border: 3px solid goldenrod;
  border-radius: 38% 62% 60% 40% / 50% 49% 51% 50%;
  width: 400px;
  height: 400px;
  margin-bottom: 3rem;
  margin-top: 2rem;
}
.drop-image1 img {
  object-fit: cover;
  width: 100%;
  height: 100%;
  border-radius: 38% 62% 60% 40% / 50% 49% 51% 50%;
}
.drop-image2 {
  display: flex;
  justify-items: center;
  align-items: center;
  border: 3px solid goldenrod;
  border-radius: 58% 42% 35% 65% / 48% 50% 50% 52%;
  width: 400px;
  height: 400px;
  margin-bottom: 3rem;
  margin-top: 2rem;
}
.drop-image2 img {
  object-fit: cover;
  width: 100%;
  height: 100%;
