.img-logo {
  height: 180px;
  width: 360px;
}

.btnFlex {
  display: flex;
  flex-direction: column;
  align-items: center;
  margin-top: 30vh;
  /* justify-content: center; */
}
@media screen and (max-width: 820px) {
  .btnFlex {
    display: none;
  }
}
.btn-event {
  color: #fff;
  cursor: pointer;
  font-size: 18px;
  font-weight: 800;
  line-height: 10px;
  position: relative;
  text-decoration: none;
  text-transform: uppercase;
  width: -webkit-fit-content;
  width: -moz-fit-content;
  width: fit-content;
}
@media (min-width: 600px) {
  .btn-event {
    margin: 0 3em 1.2em;
  }
}
.btn-event:hover {
  text-decoration: none;
  color: #fff;
  border: 0px;
}

.btn-2 {
  position: relative;
  overflow: hidden;
  -webkit-transition: 0.3s;
  transition: 0.3s;
  border: 0px;
}
button :focus {
  outline: none !important;
}
a :focus {
  outline: none !important;
}
.nav-link :focus {
  outline: none !important;
}
.btn-2:after {
  position: absolute;
  -webkit-transition: 0.3s;
  transition: 0.3s;
  content: "";
  width: 0;
  left: 50%;
  bottom: 0;
  height: 2px;
  background: #f7f7f7;
  /* border: 0px; */
}
.btn-2:hover {
  cursor: pointer;
}
.btn-2:hover:after {
  width: 100%;
  left: 0;
}
.btn-2:focus {
  box-shadow: none;
}
.active-bottom {
  border-bottom: 2px solid white;
}

.borderImgWrapper {
  border: 1px solid;
  -webkit-text-fill-color: transparent;
  background: -webkit-linear-gradient(
    355.25deg,
    #d19a08,
    #fedb7e 27.6%,
    #eac460 50%,
    #d19a08 66.15%,
    #d19a08
  );
  background: linear-gradient(
    94.75deg,
    #d19a08,
    #fedb7e 27.6%,
    #eac460 50%,
    #d19a08 66.15%,
    #d19a08
  );
  -webkit-background-clip: text;
  padding: 4px;
}

.title {
  text-align: center;
  width: -webkit-fit-content;
  width: -moz-fit-content;
  width: fit-content;
  margin: auto;
  margin-top: 20px;
  margin-bottom: 30px;
  letter-spacing: -2px;
  background: -webkit-linear-gradient(
    355.25deg,
    #d19a08,
    #fedb7e 27.6%,
    #eac460 50%,
    #d19a08 66.15%,
    #d19a08
  );
  background: linear-gradient(
    94.75deg,
    #d19a08,
    #fedb7e 27.6%,
    #eac460 50%,
    #d19a08 66.15%,
    #d19a08
  );
  -webkit-background-clip: text;
  -webkit-text-fill-color: transparent;
  font-size: 35px !important;
  font-family: "Montserrat", sans-serif;
  /* font-size: 2.2rem; */
  /* text-shadow: 2px 2px 2px #aaa; */
}

.title:hover {
  color: linear-gradient(
    94.75deg,
    #d19a08,
    #fedb7e 27.6%,
    #eac460 50%,
    #d19a08 66.15%,
    #d19a08
  );
}

.homeCard {
  background: rgba(255, 255, 255, 0.1);
  box-shadow: 0 8px 30px 0 goldenrod;
  backdrop-filter: blur(9px);
  -webkit-backdrop-filter: blur(9px);
  border-radius: 10px;
  border: 1px solid rgba(255, 255, 255, 0.18);
  color: rgb(252, 252, 252);
  padding: 10px !important;
}

.nav-link {
  padding: 0 !important;
}

.header-link {
  font-size: 1.05rem;
}

@media (min-height: 400px) {
  .header-link {
    font-size: 1.3rem;
  }
}

@media (min-height: 520px) {
  .header-link {
    font-size: 1.5rem;
  }
}

.gradient-outline-rounded {
  display: flex;
  justify-content: center;
  align-items: center;
  box-sizing: border-box;
  background: -webkit-linear-gradient(
    355.25deg,
    #d19a08,
    #fedb7e 27.6%,
    #eac460 50%,
    #d19a08 66.15%,
    #d19a08
  );
  background: linear-gradient(
    94.75deg,
    #d19a08,
    #fedb7e 27.6%,
    #eac460 50%,
    #d19a08 66.15%,
    #d19a08
  );
  width: -webkit-fit-content;
  width: -moz-fit-content;
  width: fit-content;
  box-shadow: 0 8px 8px rgba(0, 0, 0, 0.2);
  border-radius: 50%;
  object-position: center;
  object-fit: cover;
  position: relative;
  margin-bottom: 50px;
  padding: 2px;
}

